import '@@assets/styles/views/attendance/setup/form.scss';
import { ACTIVE_PAGE__ } from "@@addons/functions/active_page";
import { tokenLogin } from "@@addons/functions/login";
import { LitElement, html, css } from 'lit';
import { customElement, property, query, queryAll } from 'lit/decorators.js';
import '../../forms/sms';
import '../links';

@customElement("attendance-setup-form-edit-sms")
export class AttendanceSetupFormEditSMS extends LitElement {
  constructor() {
    super();
    ACTIVE_PAGE__();
  }

  //  @query(identifier)
  //  private _identifier?: any;

  //  @queryAll(identifier)
  //  private _identifier?: any;

  @property({ type: String })
  public email?: string;

  @property({ type: Number })
  public mId?: number;

  @property({ type: Number })
  public clientId?: number;

  async connectedCallback() {
    super.connectedCallback();
    await tokenLogin(this.email, this.mId, "client_tokenLogin");
  }

  disconnectedCallback() { }

  render() {
    return html`
      <attendance-setup-form-edit-links></attendance-setup-form-edit-links>
      <attendance-setup-form-sms CLIENT_ID="${this.clientId}"></attendance-setup-form-sms>
    `;
  }

  firstUpsmsd() { }

  createRenderRoot() {
    return this;
  }
}


